import React from 'react'
import '../assets/style/post.sass'
import {Link} from 'gatsby'

const PostTemplate = ({
    content,
    title,
    acf,
    type,
    slug
  }) => {
    return (
      <>
        <div className="post-preview">
          <div className="container">
            <div className="breadcrumbs">
              <Link to={'/'}>Главная</Link> / 
              <Link to={slug}>{` ${type} /`}</Link>
            </div>
            <div className="post-preview__logo">
              {acf.logo ? <img src={acf.logo.source_url} alt="log"/> : null}
            </div>
          </div>
        </div>
        <div className="post">
          <div className="container">
            <h2 className="post__title">
              {title}
            </h2>
            <div className="post__content" dangerouslySetInnerHTML={{ __html: content }} />
            <a className="post__link" href="/" target="_blank">Получить бонус</a>
          </div>
        </div>
      </>
    )
  }

export default PostTemplate;